import React from "react";
import { Navigate, useParams } from "react-router-dom";

export default function Onebook({data}){
    const {id} = useParams();
    const res = data.filter((ele)=>(ele.bookisbn)===id);
    if(res.length===0){
        return <Navigate to="/" replace/>;
   }
    return (
        <div className="onebook">
         <div className="card" style={{width: "18rem"}}>
                    <div className="card-body">
                        <h5 className="card-title">{res[0].bookname}</h5>
                        {/* <img src="..." className="card-img-top" alt="..."/> */}
                        <h6 className="card-subtitle mb-2 text-body-secondary"><img src="data:image/jpeg;base64,"/></h6>
                        <p className="card-text"> Book-ISBN:{id}<br />
                            Book-category:{res[0].bookcategory}<br />
                            Row-number:{res[0].rownumber}<br />
                            Book-cost:{res[0].cost}<br /></p>
                        {/* <a href="#" className="card-link">Card link</a>
                        <a href="#" className="card-link">Another link</a> */}
                    </div>
                </div>
        </div>
    );
}